.related-products {
  grid-column-start: 1;
  grid-column-end: 4;
  grid-row-start: 3;
  grid-row-end: 5;
}

.related-products__title {
  font-size: 24px;
  line-height: 24px;
  font-weight: 400;
  color: @text-title;
  margin-top: 25px;
  margin-bottom: 34px;
  padding-right: 22px;
  background-color: white;
  min-width: 226px;
  width: min-content;

  @media (min-width: @tablet-width) {
    min-width: 338px;
    margin-top: 45px;
    margin-bottom: 80px;
    font-size: 36px;
    line-height: 36px;
    padding-right: 35px;
    padding-left: 37px;
  }

  @media (min-width: @desktop-width) {
    margin-top: 76px;
    padding-left: 40px;
    transform: translateX(-40px);
  }
}

.related-products__list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.related-products__item {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-template-rows: repeat(3, auto);

  @media (min-width: @tablet-width) {
    grid-template-columns: 1fr auto 90px;
    grid-template-rows: repeat(2, auto);
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: 1fr 205px 45px 244px;
    grid-template-rows: 40px;
    column-gap: 38px;
  }
}
