/* Global */
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}
/* colors */
/* text */
/* viewports */
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.page {
  height: 100%;
}
.page__body {
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  margin: 0;
  padding: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: #444444;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden;
}
.container {
  width: 280px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 708px;
  }
}
@media (min-width: 1440px) {
  .container {
    width: 1220px;
    padding: 0 110px;
  }
}
/* Blocks */
.button {
  font-family: inherit;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #FFFFFF;
  background-color: #68B738;
  text-align: center;
  box-sizing: border-box;
  padding: 11px 44px;
  cursor: pointer;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .button {
    width: 244px;
    padding: 17px 26px;
  }
}
.button--min {
  padding: 10px 109px;
}
.button--grey {
  color: #444444;
  background-color: #F2F2F2;
  padding: 10px 94px;
}
.button--grey:hover {
  background-color: #ebebeb;
}
.button--grey:active {
  background-color: #ebebeb;
  color: rgba(68, 68, 68, 0.3);
}
.text-title {
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
  color: #000000;
}
@media (min-width: 768px) {
  .text-title {
    font-size: 60px;
    line-height: 60px;
  }
}
.common-text {
  font-family: "Arial", sans-serif;
  font-weight: 400;
  line-height: 18px;
  color: #444444;
}
@media (min-width: 768px) {
  .common-text {
    font-size: 16px;
    line-height: 24px;
  }
}
