    height: 59px;
  }
}
.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: center;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    flex-direction: row;
    width: 440px;
    flex-wrap: wrap;
    justify-content: space-between;
    align-self: center;
    padding-top: 16px;
  }
}
@media (min-width: 1440px) {
  .main-nav__list {
    width: 478px;
    padding-top: 0;
    z-index: 3;
  }
}
.main-nav__item {
  display: block;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  text-transform: uppercase;
  padding-top: 21px;
  padding-bottom: 24px;
  border-top: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .main-nav__item {
    height: 36px;
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    padding: 0;
    border: none;
  }
}
@media (min-width: 1440px) {
  .main-nav__item {
    height: 40px;
    font-size: 20px;
    line-height: 30px;
  }
}
@media (min-width: 768px) {
  .main-nav__item--current {
    position: relative;
    cursor: pointer;
  }
  .main-nav__item--current::after {
    content: "";
    position: absolute;
    bottom: 3px;
    left: 0;
    right: 0;
    height: 2px;
    background-color: #68B738;
  }
}
@media (min-width: 1440px) {
  .page-index .main-nav__item--current::after {
    background-color: white;
  }
}
.main-nav__link {
  color: #000000;
}
@media (min-width: 1440px) {
  .main-nav__link {
    color: #FFFFFF;
  }
  .main-nav__link:hover,
  .main-nav__link:focus {
    opacity: 0.6;
  }
  .main-nav__link:active {
    opacity: 0.3;
  }
}
@media (min-width: 1440px) {
  .main-nav__link--black {
    color: #111111;
  }
}
.main-nav__toggle {
  width: 18px;
  height: 18px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 25px;
  right: 23px;
  width: 24px;
  height: 1px;
  background-color: black;
  box-shadow: 0 8px 0 0 black, 0 16px 0 0 black;
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 32px;
  right: 0;
  width: 25px;
  height: 1px;
  background-color: black;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
